<template>
  <v-app>
    <v-content>
      <div id="app">
        <v-navigation-drawer :clipped="clipped" v-model="drawer" enable-resize-watcher app dark>
            <v-list v-for="item in items" :value="item.action" :key="item.title">
              <v-list-tile-content>
                <router-link :to="item.path">
                  <v-list-tile-title @click="drawer = false">{{ item.title }}</v-list-tile-title>
                </router-link>
              </v-list-tile-content>
          </v-list>
        </v-navigation-drawer>
        <v-toolbar fixed app :clipped-left="clipped">
          <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
          <v-toolbar-title>BMW of San Antonio Sales: Make Ready</v-toolbar-title>
        </v-toolbar>
        <router-view />
      </div>
    </v-content>
  </v-app>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      drawer: false,
      clipped: false,
      items: [
        {
          title: "Requests",
          path: "/"
        },
        {
          title: "New Car",
          path: "/newcar"
        },
        {
          title: "Loan Car",
          path: "/loancar"
        },
        {
          title: "TPO",
          path: "/preowned"
        },
        {
          title: "Finance",
          path: "/finance"
        },
        {
          title: "Car Wash",
          path: "/carwash"
        },
        {
          title: "Parts",
          path: "/parts"
        },
        {
          title: "Metrics",
          path: "/metrics"
        }
      ]
    };
  }
};
</script>

<style>
nav {
  margin-bottom: 5rem;
}

#app {
  font-family: "Open Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

.bg-light {
  background-color: #006876 !important;
}

.badge {
  padding-top: 10px;
  padding-bottom: 10px;
}

p.badge {
  font-size: 18px;
}

.yesccp {
  background-color: #d9ead3;
  color: #274e13;
}

.noccp {
  background-color: #f4cccc;
}

.dropdown-backdrop {
  position: static;
}

a.nav-link,
.navbar-brand,
a.navbar-brand:hover,
span.navbar-toggler-icon,
v-group a {
  color: white;
}

.v-list__tile__content {
  -webkit-box-orient: unset;
  -webkit-box-direction: unset;
      -ms-flex-direction: unset;
          flex-direction: unset;
  margin-top: 20px;
}
</style>



